import React, { Component } from 'react'
import {Table} from 'react-materialize';
import axios from 'axios';

export default class PositionTable extends Component {
  constructor(props){
    super(props);
    this.state = {size: 3}
  }

  componentDidMount() {
    console.log("HERE");
    axios.get(`https://hspapi.herokuapp.com/v1/positions`)
      .then(res => {
          console.log(res);
        const positions = JSON.parse(res.data.info);
        this.setState({ positions });
      });
  }

  getRows(trades){
    if (!trades) return null;
    return trades.map(trade => {
      return <tr><td>{trade.symbol}</td><td>{trade.qty}</td><td>{trade.current_price}</td></tr>
    });
  }

  render(){
    return(
      <div className='container'>
        <Table>
          <thead>
            <tr>
                <th>Symbol</th>
                <th>Quantity</th>
                <th>Current Price</th>
            </tr>
          </thead>
            <tbody>
              {this.getRows(this.state.positions)}
            </tbody>
          </Table>
      </div>
    )
  }
}